// src/redux/reducers/userReducer.js

const initialState = {
  user: {
    _id: null,
    username: "",
    email: "",
    friends: [],
    expensis: [], // canonical list
  },
};

// --- Helpers that look into flat fields OR doc.* and inp.* ---
function pick(obj, paths) {
  for (let p of paths) {
    const v = p
      .split(".")
      .reduce((o, k) => (o && o[k] !== undefined ? o[k] : undefined), obj);
    if (v !== undefined && v !== null && v !== "") return v;
  }
  return undefined;
}
function getDesc(e) {
  return (
    pick(e, [
      "description",
      "inp.description",
      "doc.description",
      "doc.inp.description",
    ]) || ""
  );
}
function getAmt(e) {
  return (
    pick(e, ["amount", "inp.amount", "doc.amount", "doc.inp.amount"]) || ""
  );
}
function getDate(e) {
  return (
    pick(e, [
      "date",
      "inp.date",
      "createdAt",
      "updatedAt",
      "doc.date",
      "doc.inp.date",
      "doc.createdAt",
      "doc.updatedAt",
    ]) || ""
  );
}
function getWho(e) {
  return pick(e, ["user", "friend", "paidBy", "with", "doc.user"]) || "";
}

function expSig(e) {
  return (
    String(getDesc(e)).trim() +
    "|" +
    String(getAmt(e)) +
    "|" +
    String(getDate(e)) +
    "|" +
    String(getWho(e))
  );
}

function choosePreferReal(existing, incoming) {
  const exId = pick(existing, ["_id", "id", "doc._id", "doc.id"]);
  const inId = pick(incoming, ["_id", "id", "doc._id", "doc.id"]);
  const exIsTemp = exId && String(exId).startsWith("temp-");
  const inIsTemp = inId && String(inId).startsWith("temp-");
  if (!exId && inId) return incoming;
  if (exIsTemp && !inIsTemp) return incoming;
  return incoming || existing;
}

function mergeExpenseArrays(baseArr, incomingArr) {
  const bySig = new Map();
  (baseArr || []).forEach((e) => bySig.set(expSig(e), e));
  (incomingArr || []).forEach((e) => {
    const s = expSig(e);
    bySig.set(s, bySig.has(s) ? choosePreferReal(bySig.get(s), e) : e);
  });
  return Array.from(bySig.values());
}

export const userReducer = (state = initialState, action) => {
  switch (action.type) {
    case "AddUser": {
      const doc = action.payload || {};
      const incoming =
        doc &&
        doc.user &&
        (doc.user.username ||
          doc.user.friends ||
          doc.user.expensis ||
          doc.user.expenses)
          ? doc.user
          : doc;

      const nextFriends = Array.isArray(incoming.friends)
        ? incoming.friends
        : state.user.friends;

      const incomingExpRaw = Array.isArray(incoming.expensis)
        ? incoming.expensis
        : Array.isArray(incoming.expenses)
        ? incoming.expenses
        : undefined;

      const nextExpensis = Array.isArray(incomingExpRaw)
        ? mergeExpenseArrays(state.user.expensis, incomingExpRaw)
        : state.user.expensis;

      return {
        user: {
          ...state.user,
          ...incoming,
          friends: nextFriends,
          expensis: nextExpensis,
        },
      };
    }

    case "APPEND_EXPENSES": {
      const add = Array.isArray(action.payload)
        ? action.payload
        : [action.payload];
      return {
        user: {
          ...state.user,
          expensis: mergeExpenseArrays(state.user.expensis, add),
        },
      };
    }

    case "DELETE_EXPENSE": {
      const targetSig = expSig(action.payload);
      const targetId = pick(action.payload, ["_id", "id", "doc._id", "doc.id"]);
      const filtered = (state.user.expensis || []).filter((e) => {
        const id = pick(e, ["_id", "id", "doc._id", "doc.id"]);
        if (targetId && id && String(id) === String(targetId)) return false;
        return expSig(e) !== targetSig;
      });
      return { user: { ...state.user, expensis: filtered } };
    }

    case "LOGOUT":
      return initialState;

    default:
      return state;
  }
};
