import React from "react";
import { NavLink } from "react-router-dom";
import { connect } from "react-redux";
import "../styles/dashHeader.css";

const DashHeader = (props) => {
  return (
    <nav className="DashboardNav fixed-top">
      <NavLink to="/Dashboard" className="nav-brand">
        <div className="brand-logo"></div>
        <div className="brand-text">
          <div className="brand-title">Personal Budget Tracker</div>
          <div className="brand-sub">Dashboard</div>
        </div>
      </NavLink>

      <div className="nav-right">
        <span className="user-chip">
          <img
            className="profile"
            src={require("../images/person-profile.png")}
            alt=""
          />
          <span className="user-name">{props.user.username}</span>
        </span>

        <NavLink to="/login">
          <button
            className="logoutbtn"
            onClick={() => {
              localStorage.removeItem("jwtToken");
            }}
          >
            Log Out
          </button>
        </NavLink>
      </div>
    </nav>
  );
};

const mapStateToProps = (state) => ({ user: state.user });
export default connect(mapStateToProps)(DashHeader);
